"use client";

import { useEffect } from "react"; // üí° useEffect Ï∂îÍ∞Ä
import { Pagination } from "../common/Pagination";
import { useRouter } from "next/navigation"; // üí° useRouter Ï∂îÍ∞Ä
import { createClient } from "@/utils/supabase/client"; // üí° supabase client Ï∂îÍ∞Ä

export default function MainPageWrapper({
  totalPosts,
  currentPage,
  postsPerPage,
}: {
  totalPosts: number;
  currentPage: number;
  postsPerPage: number;
}) {
  const router = useRouter(); // üí° router ÏÇ¨Ïö©

useEffect(() => {
    const supabase = createClient();

    // 1. posts Ï±ÑÎÑê Íµ¨ÎèÖ
    const postsChannel = supabase
      .channel("public:posts") // üí° Ï±ÑÎÑê Ïù¥Î¶Ñ 1
      .on(
        "postgres_changes",
        {
          event: "*",
          schema: "public",
          table: "posts", // üëà posts ÌÖåÏù¥Î∏î Í∞êÏßÄ
        },
        (payload) => {
          console.log("Posts table changed!", payload);
          router.refresh();
        }
      )
      .subscribe();

    // 2. [Ï∂îÍ∞Ä] comments Ï±ÑÎÑê Íµ¨ÎèÖ
    const commentsChannel = supabase
      .channel("public:comments")
      .on(
        "postgres_changes",
        {
          event: "*",
          schema: "public",
          table: "comments", // üëà comments ÌÖåÏù¥Î∏î Í∞êÏßÄ
        },
        (payload) => {
          console.log("Comments table changed!", payload);
          // üí° ÎåìÍ∏ÄÏù¥ Î≥ÄÍ≤ΩÎêòÏñ¥ÎèÑ ÌéòÏù¥ÏßÄ ÏÉàÎ°úÍ≥†Ïπ®
          router.refresh(); 
        }
      )
      .subscribe();

    // 3. [ÏàòÏ†ï] Ïª¥Ìè¨ÎÑåÌä∏Í∞Ä ÏÇ¨ÎùºÏßà Îïå "Îëê Ï±ÑÎÑê Î™®Îëê" Íµ¨ÎèÖ Ìï¥Ï†ú
    return () => {
      supabase.removeChannel(postsChannel);
      supabase.removeChannel(commentsChannel);
    };
  }, [router]);

  const handlePageChange = (newPage: number) => {
    router.push(`/?page=${newPage}`);
  };

  return (
    <>
      <Pagination
        currentPage={currentPage}
        totalPosts={totalPosts}
        postsPerPage={postsPerPage}
        visibleRange={2}
        onPageChange={handlePageChange}
      />
    </>
  );
}