import React from "react";

interface PaginationProps {
  currentPage: number;
  totalPosts: number;
  postsPerPage: number;
  visibleRange?: number; // ÌòÑÏû¨ ÌéòÏù¥ÏßÄ Í∏∞Ï§ÄÏúºÎ°ú ÏñëÏ™Ω Î™á Í∞úÏî© Î≥¥Ïó¨Ï§ÑÏßÄ
  onPageChange: (page: number) => void;
}

export function Pagination({
  currentPage,
  totalPosts,
  postsPerPage,
  visibleRange = 1,
  onPageChange,
}: PaginationProps) {
  const totalPages = Math.ceil(totalPosts / postsPerPage);

  // ÏãúÏûë/ÎÅù ÌéòÏù¥ÏßÄ Í≥ÑÏÇ∞
  const startPage = Math.max(1, currentPage - visibleRange);
  const endPage = Math.min(totalPages, currentPage + visibleRange);

  // ÌéòÏù¥ÏßÄ Î∞∞Ïó¥ ÏÉùÏÑ±
  const pages = [];
  for (let i = startPage; i <= endPage; i++) {
    pages.push(i);
  }

  // ÌéòÏù¥ÏßÄ Ïù¥Îèô Ìï∏Îì§Îü¨
  const handlePrev = () => {
    if (currentPage > 1) onPageChange(currentPage - 1);
  };

  const handleNext = () => {
    if (currentPage < totalPages) onPageChange(currentPage + 1);
  };

  return (
    <div className="flex justify-center gap-2 pt-4">
      {/* Ïù¥Ï†Ñ Î≤ÑÌäº */}
      <button
        onClick={handlePrev}
        disabled={currentPage === 1}
        className={`px-3 py-1 rounded text-sm transition-colors ${
          currentPage === 1
            ? "bg-gray-900 text-gray-600 cursor-not-allowed"
            : "bg-gray-800 text-gray-400 hover:bg-gray-700"
        }`}
      >
        Previous
      </button>

      {/* ÌéòÏù¥ÏßÄ Î≤àÌò∏ Î≤ÑÌäº */}
      {pages.map((page) => (
        <button
          key={page}
          onClick={() => onPageChange(page)}
          className={`px-3 py-1 rounded text-sm transition-colors ${
            page === currentPage
              ? "bg-gray-700 text-white"
              : "bg-gray-800 text-gray-400 hover:bg-gray-700"
          }`}
        >
          {page}
        </button>
      ))}

      {/* Îã§Ïùå Î≤ÑÌäº */}
      <button
        onClick={handleNext}
        disabled={currentPage === totalPages}
        className={`px-3 py-1 rounded text-sm transition-colors ${
          currentPage === totalPages
            ? "bg-gray-900 text-gray-600 cursor-not-allowed"
            : "bg-gray-800 text-gray-400 hover:bg-gray-700"
        }`}
      >
        Next
      </button>
    </div>
  );
}
